import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import AlphabetRow from "./AlphabetRow";
import NumbersColumns from "./NumbersColumns";
import LoginLogout from "../application/LoginLogout";

import PostsButtonGroup from "../posts/PostsButtonGroup";

import ButtonGroup from "react-bootstrap/ButtonGroup";
import Button from "react-bootstrap/Button";

import "./Spreadsheet.css";

import {
  // selectSpreadsheetValue,
  selectSpreadsheetProtoData,
  selectSpreadsheetData,
  selectSpreadsheetFormulaValue,
  selectSpreadsheetFormulaRowIndex,
  selectSpreadsheetFormulaColumnIndex,

  add_row_under,
  add_row_before,

  delete_row,
  add_column_after,
  add_column_before,
  delete_column,

  update_formula,
  update_data,
  selectSpreadsheetTitle,

  new_empty_spreadsheet,

  //toggle_expand_view,
  //selectSpreadsheetExpand
} from "./spreadsheetSlice";

import {
  selectApplication
} from "../application/applicationSlice";

export default function SpreadsheetLayout(props) {
  const [expandView, toggle_expand_view] = useState(false);
  const dispatch = useDispatch();
  const data = useSelector(selectSpreadsheetData);
  const title = useSelector(selectSpreadsheetTitle);
  //const expandView = useSelector(selectSpreadsheetExpand);

  const userProfile = useSelector(selectApplication);
  //console.log(userProfile);

  // let username = !!userProfile?.email && userProfile.email.length > 0 ? userProfile.email : "Anonymous";
  let numberOfX = data[0].length - 1;
  let numberOfY = data.length;

  //  console.log(title);

  return (
    <div>
      <div className="excelstyle">
        <div
          className="title"
          style={{
            display: "flex",
            justifyContent: "space-between",
            padding: ".4rem",
          }}
        >
          {userProfile?.avatarUrl && userProfile.avatarUrl.length > 10 ?
            <img
              src={userProfile.avatarUrl}
              alt=""
              style={{
                verticalAlign: 'middle',
                width: '30px',
                height: '30px',
                borderRadius: '50%',
                filter: 'grayscale(100%)',
                objectFit: 'cover'
              }} /> : null}

          {!!title ? <span style={{ marginLeft: "1rem" }}>{title}</span> : <span>Calc</span>}
         <LoginLogout />
        </div>
        <div className="icon-bar">

          {!!userProfile?.email ?
            <PostsButtonGroup
             expandView={expandView}
             toggle_expand_view={toggle_expand_view}
             quizString={props?.quizString}
             title={props?.title}
             answer={props?.answer}
             answerIsRight={props?.answerIsRight}             
             />
            :
            <ButtonGroup aria-label="Posts Buttons" size="sm">
              <Button variant="outline-secondary"
                onClick={() => dispatch(new_empty_spreadsheet())}
                data-toggle="tooltip"
                data-placement="bottom"
                title=" "
              ></Button>
              <Button variant="outline-secondary"
                onClick={() => toggle_expand_view(!expandView)}
                data-toggle="tooltip"
                data-placement="bottom"
                title={expandView ? " " : " "}
              >{expandView ? "" : ""}</Button>

            </ButtonGroup>}

          <ButtonGroup aria-label="Rows Buttons" size="sm">
            <Button
              variant="outline-secondary"
              data-toggle="tooltip"
              data-placement="bottom"
              title="  "
              onClick={() => dispatch(add_row_under())}
            >
              +_
            </Button>
            <Button
              variant="outline-secondary"
              data-toggle="tooltip"
              data-placement="bottom"
              title="  "
              onClick={() => dispatch(add_row_before())}
            >
              +-
            </Button>
            <Button
              variant="outline-secondary"
              data-toggle="tooltip"
              data-placement="bottom"
              title="  "
              onClick={() => dispatch(delete_row())}
            >
              x-
            </Button>
          </ButtonGroup>

          <ButtonGroup aria-label="Columns Buttons" size="sm">
            <Button
              variant="outline-secondary"
              data-toggle="tooltip"
              data-placement="bottom"
              title="  "
              onClick={() => dispatch(add_column_after())}
            >
              +|
            </Button>
            <Button
              variant="outline-secondary"
              data-toggle="tooltip"
              data-placement="bottom"
              title="  "
              onClick={() => dispatch(add_column_before())}
            >
              |+
            </Button>
            <Button
              variant="outline-secondary"
              data-toggle="tooltip"
              data-placement="bottom"
              title="  "
              onClick={() => dispatch(delete_column())}
            >
              x|
            </Button>
          </ButtonGroup>
        </div>
        <FormulaBlock />
        {expandView ? <div
          className="cells"
          style={{
            gridTemplateColumns: `40px repeat(${numberOfX + 1
              }, calc((100% - 50px) / ${numberOfX + 1}))`,
            gridTemplateRows: `repeat(${numberOfY}, 25px)`,
          }}
        >
          <div className="cells__spacer"></div>
          <AlphabetRow x={numberOfX} />
          <NumbersColumns y={numberOfY} />
          <ActiveCells />
        </div> : null}

      </div>
    </div>
  );
}

function ActiveCells() {
  const data = useSelector(selectSpreadsheetData);
  //  selectSpreadsheetFormulaValue,
  const formulaRowIndex = useSelector(selectSpreadsheetFormulaRowIndex);
  const formulaColumnIndex = useSelector(selectSpreadsheetFormulaColumnIndex);

  return (
    <>
      {data.map((row, rowIndex) => {
        return row.map((column, columnIndex) => {
          return (
            <Cell
              key={"" + rowIndex + "_" + columnIndex}
              rowIndex={rowIndex}
              columnIndex={columnIndex}
              active={
                formulaRowIndex === rowIndex &&
                  formulaColumnIndex === columnIndex
                  ? true
                  : false
              }
            />
          );
        });
      })}
    </>
  );
}

function Cell(props) {
  const data = useSelector(selectSpreadsheetData)[props.rowIndex][
    props.columnIndex
  ];
  const proDataValue = useSelector(selectSpreadsheetProtoData)[props.rowIndex][
    props.columnIndex
  ];

  const dispatch = useDispatch();
  const [value, setValue] = useState(data);

  useEffect(() => {
    setValue(data);
  }, [data]);

  function onKeyPressOnInput(e) {
    if (e.key === "Enter") {
      let valueChecked = isNaN(value) ? !!value ? value : "" : +value;
      dispatch(update_data(
        {
          rowIndex: props.rowIndex,
          columnIndex: props.columnIndex,
          value: valueChecked,
        }
      ));
      dispatch(update_formula({
        rowIndex: props.rowIndex,
        columnIndex: props.columnIndex,
        value: !!value ? value : "",
      }));
    }
  }

  function clicked() {
    dispatch(update_formula({
      rowIndex: props.rowIndex,
      columnIndex: props.columnIndex,
      value: proDataValue,
    }));
  }

  return (
    <input
      type="text"
      className={props.active ? "cells__input__active" : "cells__input"}
      value={value}
      onChange={(e) => setValue(e.target.value)}
      onClick={() => clicked()}
      onKeyPress={(e) => onKeyPressOnInput(e)}
    />
  );
}

function FormulaBlock() {
  const formulaValue = useSelector(selectSpreadsheetFormulaValue);
  const formulaRowIndex = useSelector(selectSpreadsheetFormulaRowIndex);
  const formulaColumnIndex = useSelector(selectSpreadsheetFormulaColumnIndex);

  const [formula, setFormula] = useState('');

  const dispatch = useDispatch();

  function onKeyPressOnInput(e) {
    if (e.key === "Enter") {
      handleSubmit();
    }
  }

  function handleSubmit() {
    let valueChecked = isNaN(formula) ? !!formula ? formula : "" : +formula;
    dispatch(update_data({
      rowIndex: formulaRowIndex,
      columnIndex: formulaColumnIndex,
      value: valueChecked,
    }));
    setFormula("");

    // dispatch(update_formula({
    //   rowIndex: formulaRowIndex,
    //   columnIndex: formulaColumnIndex,
    //   value: "",
    // }));
  }

  return (
    <div className="cell-content">
      <div>fx</div>
      {/* <div > */}
      <button onClick={() => handleSubmit()}>
        <span >&#10003;</span>
      </button>
      {/* </div> */}
      <div>
        <input
          style={{
            width: "100%",
            border: "none",
            outline: "none",
            marginLeft: "3px",
            fontSize: "1.2rem"
          }}
          value={formula === "" ? formulaValue : formula}
          onChange={(e) => setFormula(e.target.value)}
          onKeyPress={(e) => onKeyPressOnInput(e)}
        />
      </div>
    </div>
  );
}
